@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Fonts Imported */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

/* Body styles */
body {
  font-family: 'Roboto', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  color: #fff;
  width: 100vw;
  height: 100vh;
  background: rgb(212, 180, 243);
}

/* Header */

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  color: #333;
  padding: 10px 15px;
}

.header img {
  width: 50px;
  border-radius: 50%;
  padding: 5px;
}

.header h2 {
  padding: 5px;
  font-size: 1rem;
  font-weight: 400;
}

/* Navbar */
.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 150px;
  margin-top: 70px;
  background: #680568;
}

.navbar img {
  width: 100px;
  border-radius: 50%;
  cursor: pointer;
}

/* Cherished */
.cherished {
  background: #fff;
  color: #333;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 0;
}

.cherished h2 {
  font-weight: 400;
}

/* Links */
.links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 30px;
}

.links ul {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
  width: 100%;
}

.links ul li {
  margin: 20px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background: #fff;
}

.links ul li a {
  color: #111;
  text-decoration: none;
  text-transform: uppercase;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 18px 0px 20px rgba(0, 0, 0, 0.1),
    -18px 0px 20px rgba(0, 0, 0, 0.1), 0px 18px 20px rgba(0, 0, 0, 0.1),
    0px -18px 20px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
}

.links ul li a:hover {
  color: #a300a3;
  transition: all 0.5s ease;
}

.links ul li:hover {
  color: #a300a3;
  background-color: #fff;
  transition: all 0.5s ease;
}

/* Footer */
.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  width: 100%;
  box-shadow: 30px 0px 40px rgba(0, 0, 0, 0.1),
    -30px 0px 40px rgba(0, 0, 0, 0.1);
}

.footer img {
  width: 50px;
  border-radius: 50%;
  padding: 10px;
  cursor: pointer;
}

.footer h2 {
  font-size: 1rem;
  font-weight: 400;
  color: #000;
}

/* Mobile Styles */

@media (max-width: 700px) {
  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }
  .navbar img {
    margin-top: 1.45rem;
  }

  .navbar ul {
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
}

